import axios from "axios";
import React from "react";
import swal from "sweetalert";
import {useHistory,useParams} from "react-router-dom"

export default function LatihanUpdate() {
  let history = useHistory()
  let {id}= useParams()
  const [disableSubmit, setDisableSubmit] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(false);
  const [values, setValues] = React.useState({
    username: "",
    name: "",
    jenis_kelamin: "",
    email: "",
  
  });
  const [error, setError] = React.useState({
    username: "",
    name: "",
    jenis_kelamin: "",
    email: "",
   
  });
  const getArtikelDetail = async()=>{
    const url=`http://belajar-react.smkmadinatulquran.sch.id/api/users/${id}`
    try {
      let response = await axios.get(url)
      setValues((values)=>({
        ...values,
        username: response.data.data.username,
        name : response.data.data.name,
        email : response.data.data.email,
        jenis_kelamin : response.data.data.jenis_kelamin,
        id : response.data.data.id
      }))
     
    } catch (error) {
      
    }
  }

  React.useEffect(()=>{
    getArtikelDetail()
  },[])

  const handleChange = (e) => {
    const key = e.target.id;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "username")
      if (value === "") {
        error.username = "Username Tidak Boleh Kosong";
      } else {
        error.username = "";
      }
    if (key === "name")
      if (value === "") {
        error.name = " Nama Tidak Boleh Kosong";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else {
        error.name = "";
      }

      if (key === "jenis_kelamin")
      if (value === "") {
        error.jenis_kelamin= " Nama Tidak Boleh Kosong";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else {
        error.enis_kelamin = "";
      }
    if (key === "email") {
      if (value === "") {
        error.email = " Anda Harus Memasukan Email";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else if (value.indexOf("@") === -1 || value.indexOf(".") === -1 ) {
        error.email = "Harap Masukan Email Dengan Benar ";
      } else {
        error.email = "";
      }
    }
   
    disableSubmitHandle();
  };
  const disableSubmitHandle = () => {
    console.log(values)
    if (
      values.username === "" &&
      values.name === "" &&
      values.jenis_kelamin === "" &&
      values.email === "" 
     
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit(true);
    }
  };
  //   const handelUpload = (e) => {
  //     e.preventDefault();
  //     let reader = new FileReader();

  //     setValues((values) => ({
  //       ...values,
  //       thumbnail: e.target.files[0],
  //     }));
  //     reader.onloadend = () => {
  //       if (reader.readyState === 2) {
  //         setImagePreview(reader.result);
  //       }
  //     };
  //     reader.readAsDataURL(e.target.files[0]);
  //   };
  //   console.log(values);
  console.log(values);
  const handleSubmit = async (e) => {
    e.preventDefault();
   
    const url = `http://belajar-react.smkmadinatulquran.sch.id/api/users/update/${values.id}`;
    const formData = new FormData();
    formData.append("username", values.username);
    formData.append("name", values.name);
    
    formData.append("email", values.email);
    formData.append("jenis_kelamin", values.jenis_kelamin);
    formData.append("id",values.id)
    formData.append("_method","put")
    setIsLoading(true);

    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data ",
        },
      });
      console.log(result);
      setIsLoading(false);
      swal("Good job!", "You clicked the button!", "success");
      history.push("/latihan-detail")
    } catch (error) {
      console.log("error");
      setIsLoading(false);
      swal("Good job!", "You clicked the button! but something is Error", "error");
    }
  };

  return (
    <React.Fragment>
      <div className="font-sans border-2  px-80">
        <h1 className="text-3xl font-bold text-center py-5">LATIHAN FORM</h1>
        <form onSubmit={handleSubmit} className="mt-5 grid gap-4">
          <div className="grid gap-2">
            <label className='text-xl' htmlFor="username">Username</label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="text"
              name="username"
              id="username"
              onBlur={handleChange}
              onChange={handleChange}
              value={values.username}
            />
          </div>
          <p className="text-red-600 italic">{error.username}</p>
          <div className="grid gap-2">
            <label className='text-xl' htmlFor="name">Nama</label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="text"
              name="name"
              id="name"
              onBlur={handleChange}
              onChange={handleChange}
              value={values.name}
            />
          </div>
          <p className="text-red-600 italic">{error.name}</p>
          <div className="grid gap-2">
            <h1>Jenis Kelamin</h1>
            <div className="flex items-center">
              <label className='text-xl' htmlFor="laki_laki">Laki-Laki</label>
              <input
                className="ml-2"
                type="radio"
                value="Laki-Laki"
                name="jenis_kelamin"
                id="laki_laki"
                checked = {values.jenis_kelamin === "Laki-Laki"}
                onBlur={handleChange}
              onChange={handleChange}
                
              />
              <label className="ml-6 text-xl" htmlFor="perempuan">
                Perempuan
              </label>
              <input
                value="Perempuan"
                className="ml-2"
                type="radio"
                name="jenis_kelamin"
                id="perempuan"
                checked = {values.jenis_kelamin === "Perempuan"}
                onBlur={handleChange}
              onChange={handleChange}
              />
            </div>
          </div>
          <p className="text-red-600 italic">{error.jenis_kelamin}</p>
          <div className="grid gap-2">
            <label className='text-xl' htmlFor="email">Email</label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="email"
              name="email"
              id="email"
              onBlur={handleChange}
              onChange={handleChange}
              value={values.email}
            />
          </div>
          <p className="text-red-600 italic">{error.email}</p>
          
          
          <button
            disabled={disableSubmit}
            type="submit"
            className={`${disableSubmit ?'bg-red-700' : 'bg-green-400'} inline-flex p-3 w-full  flex justify-center items-center font-bold text-white uppercase text-2xl mb-10 rounded-lg `}
          >
            {isLoading ? "Memperbarui.." : "Perbarui"}
          </button>
        </form>
      </div>
    </React.Fragment>
  );
}
