import React from "react";

export default function Aside(props) {
    
  console.log(props);
  const HandleInputTagline = (e) => {
      e.preventDefault()
      
      return props.setTagline(e.target.value)
    
  };
  const HandleChange=(e)=>{
    e.preventDefault()
    if(props.tagline === ""){
        return props.setTagline("Info SMK MADINATULQURAN")
    }
  }

  const HandleInputJudul = (e) => {
    e.preventDefault()
    
    return props.setJudul(e.target.value)
  
};
const HandleChangeJudul=(e)=>{
  e.preventDefault()
  if(props.judul === ""){
      return props.setJudul("MQNEWS")
  }
}
  return (
    <aside>
      <input
        placeholder="TagLine"
        value={props.tagline}
        className="border mt-5 w-full px-2 py-3"
        type="text"
        onInput={HandleInputTagline}
        onBlur={HandleChange}
      />
      <input
        placeholder="JudulWeb"
        value={props.judul}
        className="border mt-5 w-full px-2 py-3"
        type="text"
        onInput={HandleInputJudul}
        onBlur={HandleChangeJudul}
      />
    </aside>
  );
}
