import axios from "axios";
import React from "react";
import swal from "sweetalert";
import {useHistory} from "react-router-dom"


export default function PostArtikel() {
  const [imagePreview, setImagePreview] = React.useState("");
  const [disableSubmit,setDisableSubmit] = React.useState(true)
  const [isLoading,setIsLoading] = React.useState(false)
  const [values, setValues] = React.useState({
    judul: "   ",
    artikel: "",
    thumbnail: "",
    user_id: "2",
    password: "",
    password_confirmation: "",
  });
  const [error, setError] = React.useState({
    judul: "",
    artikel: "",
    thumbnail: "",
    user_id: "2",
    password: "",
    password_confirmation: "",
  });

  let history = useHistory()

  const handleChange = (e) => {
    const key = e.target.id;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "judul")
      if (value === "") {
        error.judul = "Judul Tidak Boleh Kosong";
      } else {
        error.judul = "";
      }
    if (key === "artikel")
      if (value === "") {
        error.artikel = "Artikel Tidak Boleh Kosong";
        // } else if(value.replace(/\s/g, "").length < 10 || value.replace(/\s/g, "").length > 10){
      } else if (value.replace(/\s/g, "").length < 10) {
        error.artikel = "Isi Artikel terlalu Sedikit";
      } else {
        error.artikel = "";
      }
    if (key === "password") {
      if (value === "") {
        error.password = "Password Tidak Boleh Kosong";
      } else {
        error.password = "";
      }
    }
    if (key === "password_confirmation") {
      if (value === "") {
        error.password_confirmation = "Konfirmasi Password Tidak Boleh Kosong";
      } else if (value !== values.password) {
        error.password_confirmation = "Password tidak sama";
      } else {
        error.password_confirmation = "";
      }
    }
    disableSubmitHandle()
  };
  const disableSubmitHandle = () => {
    if (
      error.judul === "" &&
      error.artikel === "" &&
      error.password === "" &&
      error.password_confirmation === "" &&
      error.thumbnail === ""
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit (true);
    }
  };
  const handelUpload = (e) => {
    e.preventDefault();
    let reader = new FileReader();

    setValues((values) => ({
      ...values,
      thumbnail: e.target.files[0],
    }));
    reader.onloadend = () => {
      if (reader.readyState === 2) {
        setImagePreview(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };
  console.log(values);
  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = `https://api-react-2.herokuapp.com/api/artikel`;
    const formData = new FormData();
    formData.append("judul", values.judul);
    formData.append("artikel", values.artikel);
    formData.append("thumbnail", values.thumbnail);
    formData.append("user_id", values.user_id);
    setIsLoading(true)
    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data ",
        },
      });
      console.log(result);
      setIsLoading(false)
      swal("Good job!", "You clicked the button!", "success");
      history.push("/home")
    } catch (error) {
      console.log("error");
      setIsLoading(false)
      
    }
  };

  return (
    <React.Fragment>
      <div className="font-sans">
        <h1 className="text-3xl font-bold "> POST ARTIKEL BARU </h1>
        <form onSubmit={handleSubmit} className="mt-5 grid gap-4">
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="judul">
              Judul Artikel
            </label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="text"
              onChange={handleChange}
              onBlur={handleChange}
              name="judul"
              value={values.judul}
              id="judul"
            />
          </div>
          <p className="text-red-600 italic">{error.judul}</p>
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="artikel">
              Isi Artikel
            </label>
            <textarea
              className="border py-2 px-5 rounded-lg"
              name="artikel"
              id="artikel"
              cols="30"
              rows="10"
              onChange={handleChange}
              onBlur={handleChange}
              value={values.artikel}
            ></textarea>
          </div>
          <p className="text-red-600 italic">{error.artikel}</p>
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="password">
              Password
            </label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="password"
              onChange={handleChange}
              onBlur={handleChange}
              name="password"
              value={values.password}
              id="password"
            />
          </div>
          <p className="text-red-600 italic">{error.password}</p>
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="password_confirmation">
              Konfirmasi Password
            </label>
            <input
              className="border py-2 px-5 rounded-lg"
              type="password"
              onBlur={handleChange}
              onChange={handleChange}
              name="password_confirmation"
              value={values.password_confirmation}
              id="password_confirmation"
            />
          </div>
          <p className="text-red-600 italic">{error.password_confirmation}</p>
          <div>
            <img className="h-48" src={imagePreview} alt="" />
          </div>
          <div className="grid gap-1">
            <label className="uppercase" htmlFor="thumbnail">
              Thumbnail
            </label>
            <input
              className="border px-5 py-2 rounded-lg "
              type="file"
              name="thumbnail"
              onChange={handelUpload}
              id="thumbnail"
            />
          </div>
          <button
            disabled={disableSubmit}
            type="submit"
            className={`${disableSubmit ?'bg-red-600' : 'bg-green-600'} inline-flex p-3 w-full  flex justify-center items-center font-bold text-white uppercase text-2xl mb-10 rounded-lg `}
          >
            {isLoading ? "Menyimpan..." : "Simpan"}
          </button>
        </form>
      </div>
    </React.Fragment>
  );
}
