import React from "react";
import { useHistory } from "react-router";
import axios from "axios";
import { useParams } from "react-router-dom";

export default function NewsDetail() {
    let history = useHistory()
      const [artikel,setArtikel] = React.useState([])
  const gerArtikelDetail = async () => {
    const url = `https://api-react-2.herokuapp.com/api/artikel/${slug}`;
    try {
      let response = await axios.get(url);
      console.log(response.data.data);
      let data = response.data.data
      setArtikel(response.data.data);
      if(data === undefined){
          return history.push ("/berita")
      }
    } catch (error) {
      console.log("erorr");
    }
  };
  let { slug } = useParams();
  React.useEffect(() => {
    gerArtikelDetail();
  });
  return (
    <div>
      <h1 className="text-3xl mb-11">{artikel?.judul}</h1>
      {/* <p>Ini Adalah Page Ke- {params.slug}</p> */}

      <p className="mt-12">{artikel?.artikel}</p>
    </div>
  );
}
