import React from "react";
import Artikel from "../../component/artikel";
import Aside from "../../component/aside";

import axios from "axios";
import Loading from "../../component/loading";

export default function Home({ tagline, setTagline }, { judul, setJudul }) {
  const [artikels, setArtikels] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(false);
  const getArtikels = async () => {
    setIsLoading(true);
    const url = `https://api-react-2.herokuapp.com/api/artikel`;
    try {
      let response = await axios.get(url);
      console.log(response.data);
      setArtikels(response.data.data.data);
      setIsLoading(false);
    } catch (error) {
      console.log("error");
      setIsLoading(false);
    }
  };

  React.useEffect(() => {
    getArtikels();
  }, []);

  return (
    <React.Fragment>
      <div className="grid grid-cols-6 ">
        <div className="col-span-5 grid grid-cols-5 gap-5 overflow-auto px-1 py-5">
          {isLoading ? (
            <React.Fragment>
              <Loading></Loading>
              <Loading></Loading>
              <Loading></Loading>
            </React.Fragment>
          ) : (
            artikels.map((artikel, index) => (
              <Artikel
                judul={artikel.judul}
                thumbnail={artikel.thumbnail}
                alt={artikel.alt}
                konten={artikel.artikel}
                slug={artikel.slug}
                id={artikel.id}
                getArtikels={getArtikels}
              ></Artikel>
            ))
          )}
        </div>
        <div className="col-span-1">
          <Aside
            setTagline={setTagline}
            setJudul={setJudul}
            judul={judul}
            tagline={tagline}
          ></Aside>
        </div>

        {/* <div className="col-span-1">
            <ul>
              {siswaRPLs.map((siswa)=>(
                <li> {siswa}</li>
              ))}
            </ul>
          </div> */}
      </div>
    </React.Fragment>
  );
}
