import axios from "axios";
import React from "react";
import DataDetail from "../../component/latihanArtikel";

export default function LatihanDetail() {
  const [details, setDetails] = React.useState([]);
  const [isLoading, setIsLoading] = React.useState(false);
  const getArtikels = async () => {
    setIsLoading(true);
    const url = `http://belajar-react.smkmadinatulquran.sch.id/api/users/100`;
    try {
      let response = await axios.get(url);
      console.log(response.data.data);
      setDetails(response.data.data);
      setIsLoading(false);
    } catch (error) {
      console.log("error");
      setIsLoading(false);
    }
  };
  React.useEffect(() => {
    getArtikels();
  }, []);
  return (
    <React.Fragment>
        <h1 className='text-4xl font-bold text-center mb-4'> Data Detail</h1>
      <div className="border-2 p-3 shadow-2xl drop-shadow-lg mb-10 ">
         
        <div className="border">
          <div className="grid grid-cols-7 p-3 text-2xl font-bold">
            <h1>Username</h1>
            <h1>Nama</h1>
            <h1  className='col-start-3 col-end-5'>Email</h1>
            <h1>Jenis Kelamin</h1>
          </div>
          <div className="">
            {details.map((detail, index) => (
              <DataDetail
                username={detail.username}
                name={detail.name}
                email={detail.email}
                jenis_kelamin={detail.jenis_kelamin}
                id={detail.id}
              ></DataDetail>
            ))}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
