import React from "react";
import swal from "sweetalert";

import { Link } from "react-router-dom";
import axios from "axios";

export default function DataDetail(props) {
    const deleteHandle = async (id) =>{
        const url = `https://belajar-react.smkmadinatulquran.sch.id/api/users/hapus/${id}`;
        
        try {
          await axios.get(url);
          
          props.getArtikels()
        } catch (error) {
          
        }
      }
  return (
    <React.Fragment>
      <div className='border'>
        <div className='grid grid-cols-7 gap-x-4 p-3'>
          <h1>{props.username}</h1>
          <h1>{props.name}</h1>
          <h1 className='col-start-3 col-end-5'>{props.email}</h1>
          <h1>{props.jenis_kelamin}</h1>
          <Link
          to={`/latihan-detail/update/${props.id}`}
          className="inline-flex text-center px-4 bg-gradient-to-r from-blue-400 to-red-300 text-white py-2 rounded-md border"
        >
          Update
        </Link>
        <button onClick={()=>{
          swal({
            title: "Are you sure?",
            text: "Once deleted, you will not be able to recover this imaginary file!",
            icon: "warning",
            buttons: true,
            dangerMode: true,
          })
          .then((willDelete) => {
            if (willDelete) {
              deleteHandle(props.id)
              ;
            } else {
              swal("Your imaginary file is safe!");
            }
          });
        }} className="inline-flex px-4 bg-gradient-to-r from-yellow-500 to-red-300 text-white py-2 rounded-md border">
          
          Delete
        </button>
        </div>
      </div>
    </React.Fragment>
  );
}
