import React from "react";
import Header from "./component/header";

import { BrowserRouter, Switch, Route, Redirect} from "react-router-dom";
import About from "./pages/about";
import Contact from "./pages/contact";
import Home from "./pages/home";
import NewsDetail from "./pages/newsDetail";
import PostArtikel from "./pages/post";
import LatihanForm from "./pages/latihan post";
import Update from "./pages/update";
import DataDetail from "./pages/latihanDetail";
import LatihanUpdate from "./pages/latihanUpdate";



function App() {
  const [judul, setJudul] = React.useState("MQNEWS");

  const [tagline, setTagline] = React.useState("Info SMK MADINATULQURAN");
  // const[siswaRPLs] = React.useState(["ghozy","muhyi","danendra"])
  return (
    <BrowserRouter>
      <div className="antialiased h-screen w-full bg-gray-50 px-16">
        <div className="h-20 w-full">
          <Header tagline={tagline} judul={judul}></Header>
        </div>

        <Switch>
          <Route path="/berita" exact>
            <Home
              tagline={tagline}
              setTagline={setTagline}
              judul={judul}
              setJudul={setJudul}
            ></Home>
          </Route>
          <Route exact path='/berita/:slug'>
            <NewsDetail></NewsDetail>
          </Route>
          <Route path='/berita/update/:slug'>
            <Update></Update>
          </Route>
          <Route path="/about">
            <About></About>
          </Route>
          <Route path="/contact">
            <Contact></Contact>
          </Route>
          <Route path="/post-artikel">
            <PostArtikel></PostArtikel>
          </Route>
          <Route path="/latihan-form">
            <LatihanForm></LatihanForm>
          </Route>
          <Route exact path="/latihan-detail">
            <DataDetail></DataDetail>
          </Route>
          <Route path="/latihan-detail/update/:id">
            < LatihanUpdate></LatihanUpdate>
          </Route>
          
          <Redirect from="/contact" to="/about"/>
          <Redirect from="*" to="/berita"/>
        </Switch> 
      </div>
    </BrowserRouter>
  );
}

export default App;


